.flexContainer {
  display: flex;
  flex-direction: column;
}
.dateWrapper {
  margin-top: 145px;
  display: flex;
  align-items: center;
  column-gap: 20px;
  margin-bottom: 60px;
}
.date {
  font-size: 18px;
  font-weight: 700;
}

@media screen and (max-width: 767px) {
  .dateWrapper {
    margin-top: 40px;
    margin-bottom: 32px;
  }
}
@media screen and (min-width: 768px) and (max-width: 1279px) {
  .dateWrapper {
    margin-top: 100px;
  }
}

@media screen and (min-width: 768px) {
  .date {
    font-size: 34px;
  }
}

@media screen and (min-width: 1280px) {
  .flexContainer {
    max-width: 625px;
  }
  
  .dairyPageWrapper {
    display: flex;
    align-items: baseline;
    justify-content: space-between;
    margin-top: -472px;
    padding-top: 308px;
  }
}

.datePickerWrapper {
  padding: 5px;
  display: flex;
  cursor: pointer;
}
.datePickerWrapper svg {
  fill: #9b9faa;
  transition: all 150ms linear;
}

.datePickerWrapper:hover svg {
  fill: #808288;
}

.datePickerWrapper input {
  display: none;
}

.addBtn {
  display: block;
  font-size: 30px;
  color: #fff;
  border: none;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  margin: 0 auto;
  margin-bottom: 60px;
  background-color: #fc842d;
  box-shadow: 0px 4px 10px rgba(252, 132, 45, 0.5);
  transition: all 150ms linear;
}
.addBtn:hover {
  transform: rotate(90deg);
  background-color: #ea6e16;
}
